import React from 'react';
import { colors } from '../utils/colors';
import { TableHead, TableRow, TableCell, Typography } from '@mui/material'

export default function EnchanedTableHeader({ headerCell, checkbox = false, initialWidth }) {
    return (
        <TableHead className='mainUserTableHead'>
            <TableRow sx={{
                "& th": {
                    backgroundColor: colors.secondary
                }
            }}>
                <TableCell style={{ width: `calc(100% - ${initialWidth})`, textAlign: 'center', color: colors.primaryDark }}>
                    #
                </TableCell>
                {
                    headerCell?.map((header, index) => (
                        <TableCell key={index} style={{ width: header.width }}>
                            <Typography className='ff-Roboto fs-12 fw-medium' style={{ color: colors.primaryDark }}>
                                {header.name}
                            </Typography>
                        </TableCell>
                    ))
                }
            </TableRow>
        </TableHead>
    )
}
